import React from 'react'
import './css/Line.css'

const Line = ({
  length,
  width = 4,
  angle = 0,
  styles = {},
  children
}) => {
  let style = {
    transform: '',
    ...styles,
    width: `${width}px`,
    height: `${length}px`
  }

  style.transform += ` rotate(${angle}deg)`
  
  return (
    <div
      className="line"
      style={style}
      // onMouseEnter={() => {
      //   setHover(true)
      //   setSpinning(false)
      // }}
      // onMouseLeave={() => {
      //   setHover(false)
      //   setSpinning(true)
      // }}
    >
      {children}
    </div>
  )
}

export default Line
