import React, { useState } from 'react'
import Circle from '../Circle'
import Ring from '../Ring'
import Line from '../Line'
import './styles.css'

const CenterBranch = ({
  xy,
  initialScale = 1,
  navigate
}) => {
  const [ scale, setScale ] = useState(initialScale)
  const BASE_CIRCLE_SIZE = 50
  const RING_SIZE = BASE_CIRCLE_SIZE * 15

  return (
    <div
      className="center-branch"
      onWheel={({ deltaY }) => {
        if (deltaY > 0)
          setScale(scale + 0.02)
        else
          setScale(scale - 0.02)
      }}
      style={{
        transform: `translate(${(xy.x)}px, ${xy.y}px) scale(${scale})`
      }}
    >
      <Ring size={RING_SIZE}>
        <Line
          length={180}
          angle={90}
          styles={{
            transform: `translateX(-${180 / 2}px)`
          }}
        >
          <Circle
            text="?"
            size={BASE_CIRCLE_SIZE}
            styles={{
              bottom: `-${BASE_CIRCLE_SIZE / 2}px`,
              transform: `rotate(-${90}deg)`
            }}
          />

          <Circle
            text="PROJECTS"
            size={BASE_CIRCLE_SIZE}
            styles={{
              fontSize: `0.5rem`,
              bottom: `calc(50% - ${BASE_CIRCLE_SIZE / 2}px)`,
              transform: `rotate(-${90}deg)`
            }}
            onClick={() => navigate('projects')}
          />
        </Line>

        <Line
          length={350}
          angle={45}
          styles={{
            transform: `translateX(-${350 / 3}px) translateY(${350 / 3}px)`
          }}
        >
          <Circle
            text="?"
            size={BASE_CIRCLE_SIZE}
            styles={{
              top: `calc(30% - ${BASE_CIRCLE_SIZE / 2}px)`,
              transform: `rotate(-${45}deg)`
            }}
          />

          <Circle
            text="?"
            size={BASE_CIRCLE_SIZE}
            styles={{
              top: `calc(60% - ${BASE_CIRCLE_SIZE / 2}px)`,
              transform: `rotate(-${45}deg)`
            }}
          />

          <Circle
            text="?"
            size={BASE_CIRCLE_SIZE}
            styles={{
              top: `calc(100% - ${BASE_CIRCLE_SIZE / 2}px)`,
              transform: `rotate(-${45}deg)`
            }}
          />

          <Line
            length={45}
            styles={{
              position: 'absolute',
              top: `calc(60% + ${BASE_CIRCLE_SIZE / 2}px)`,
              left: '-10px'
            }}
          >
            <Line
              length={45}
              angle={45}
              styles={{
                position: 'absolute',
                top: `calc(100% - ${8}px)`,
                transform: `translateX(-${15}px)`
              }}
            >
              <Circle
                text="?"
                size={BASE_CIRCLE_SIZE}
                styles={{
                  top: `calc(100% - ${BASE_CIRCLE_SIZE / 2}px)`,
                  transform: `rotate(-${90}deg)`
                }}
              />
            </Line>
          </Line>

          <Line
            length={45}
            styles={{
              position: 'absolute',
              top: `calc(60% + ${BASE_CIRCLE_SIZE / 2}px)`,
              left: '10px'
            }}
          >
            <Line
              length={45}
              angle={-45}
              styles={{
                position: 'absolute',
                top: `calc(100% - ${8}px)`,
                transform: `translateX(${15}px)`
              }}
            >
              <Circle
                text="?"
                size={BASE_CIRCLE_SIZE}
                styles={{
                  top: `calc(100% - ${BASE_CIRCLE_SIZE / 2}px)`
                }}
              />
            </Line>
          </Line>
        </Line>

        <Line
          length={1}
          angle={0}
        >
          <Circle
            text="START"
            size={BASE_CIRCLE_SIZE}
            center
          />
        </Line>
      </Ring>
    </div>
  )
}

export default CenterBranch
